.login-page{
    margin: 0 auto; 
    width:350px;
    text-align: center;
    padding: 10px 20px 20px 10px;
    border: 0px solid red;
    border-radius: 15px;
    margin-bottom: 40%;
    margin-top: 10%;
    background: #32aeb1;
    color:  black;
    /* box-shadow: 0px -2px 8px 10px rgba(47,32,74,1); */
  }
  .login-page input{
    display: block;
    margin: 0 auto;
    margin-top: 15px;
    width: 290px;
    height: 30px;
    font-size: 19px;
    border: 2px solid #fff;
    border-radius: 5px;
    background: #fff;
    color:  black;
    padding: 7px;
  }
  .login-page button{
    width: 310px;
    margin-top: 15px;
    height: 34px;
    font-size: 20px;
    border: 2px solid ;
    background: #fff;
    color:  black;
    border-radius: 5px;
  }
  .login-page button:hover{
    background: #f1ebebde;
  }
  .login-page input:focus{
    outline: #000;
    border: 3px solid #eb4034;
  }
  
  .login-page a{
    color: #000;
    text-decoration: none;
  }
  .login-page button:focus{
    outline: none;
  }
  #signup{
    color:#fff;
    background: #fff;
    border: 2px solid #fff;
  }
  #signup:hover{
    color:  black;
    background: #fff;
  }
  ::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color:  black;
    opacity: 1; /* Firefox */
  }
  
  :-ms-input-placeholder { /* Internet Explorer 10-11 */
    color:  black;
  }
  
  ::-ms-input-placeholder { /* Microsoft Edge */
    color:  black;
  }